import logo from "./logo.svg";
import "./App.css";

import liff from "@line/liff";
import { useEffect, useState } from "react";

function App() {
  const [context, setContext] = useState();
  useEffect(() => {
    liff
      .init({
        liffId: "1656540125-zVgR1aVe",
      })
      .then(() => {
        const jsonString = JSON.stringify(liff.getContext());
        setContext(jsonString);
      });
  });

  return (
    <div className="App">
      <img src={logo} className="App-logo" alt="logo" />
      <div>Context: <br/> {context}</div>
    </div>
  );
}

export default App;
